import { Formik } from 'formik';
import { AddForm, AddField, InputLabel, SubmitButton } from './ContactForm.styled';
import { useFetchContactsQuery, useCreateContactMutation } from '../../redux/contactsSlice';

export const ContactForm = () => {
    const { data: contacts } = useFetchContactsQuery();
    const [createContact] = useCreateContactMutation();


    const initialValues = {
        name: '',
        number: '',
    };

    return (
        <Formik initialValues={initialValues}
            onSubmit={(values, actions) => {
                const createNewContact = (contacts, data) => {
                    if (contacts.find(contact => contact.name === data.name)) {
                        alert(`${data.name} is already in contacts`)
                    } else if (data.number.trim() === "" && data.name.trim() === "") {
                        alert(`Enter name and number`)
                    } else if (data.name.trim() === "") {
                        alert(`ontact must contain a name`)
                    } else if (data.number.trim() === "") {
                        alert(`ontact must contain a number`)
                    } else {
                        createContact(data);
                        actions.resetForm();
                    }
                };

                createNewContact(contacts, values); 
            }}>
            <AddForm>
                <InputLabel >
                    Name
                    <AddField type="text" name="name" pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                    title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"/>
                </InputLabel>
                <InputLabel >
                    Number
                    <AddField type="tel" name="number" pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                    title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"/>
                </InputLabel>
                <SubmitButton type='submit'>Add contact</SubmitButton>
            </AddForm>
        </Formik>
    );
}

